@font-face {

	font-family: 'Heebo-Medium';

	font-style: normal;

	font-weight: normal;

	src: url(/static/media/Heebo-Medium.fa56b7e2.ttf) format('woff');

	}

	@font-face {

		font-family: 'Heebo-Bold';

		font-style: normal;

		font-weight: normal;

		src: url(/static/media/Heebo-Bold.049b815f.ttf) format('woff');

		}

		@font-face {

			font-family: 'Heebo-Regular';

			font-style: normal;

			font-weight: normal;

			src: url(/static/media/Heebo-Regular.9667d671.ttf) format('woff');

			}

			@font-face {

				font-family: 'Heebo-Light';

				font-style: normal;

				font-weight: normal;

				src: url(/static/media/Heebo-Light.2bb05718.ttf) format('woff');

				}



body {

	font-family: 'Heebo-Medium' ;

	font-weight: normal;

	font-style: normal;background-color: #ffffff;

}
.sitebar1{
	background-color : #ffc200 !important;
    color: #ffffff !important;
	border:1px solid #ffc200 !important

}

.sitebar2{
	background-color : #ffffff !important;
    color: #555454 !important

}
h1,

h2,

h3,

h4,

h5,

h6 {  font-family: 'Heebo-Bold';

	color: #0b1c39;

	margin-top: 0px;

	font-style: normal;

	font-weight: 700;

	text-transform: normal

}

p {

	font-family: 'Heebo-Regular';

	color: #506172;

	font-size: 16px;

	line-height: 27px;

	margin-bottom: 15px;

	font-weight: normal

}



ul {

	margin: 0px;

	padding: 0px

}

li {

	list-style: none;

	line-height: 2.0

}

.clear {

	clear: both

}


h1 a,

h2 a,

h3 a,

h4 a,

h5 a,

h6 a {

	color: inherit

}



hr {

	border-bottom: 1px solid #eceff8;

	border-top: 0 none;

	margin: 30px 0;

	padding: 0

}

.btn {

	background: #000000;

	-moz-user-select: none;

	

	color: #fff;

	cursor: pointer;

	display: inline-block;

	font-size: 14px;

	font-weight: 500;

	

	line-height: 0;

	margin-bottom: 0;

	padding: 27px 44px;

	border-radius: 43px;

	min-width: 200px;

	margin: 10px;

	cursor: pointer;

	transition: color 0.4s linear;

	position: relative;

	z-index: 1;

	border: 0;

	overflow: hidden;

	margin: 0

}

.btn-yellow::before {

	content: "";

	position: absolute;

	left: 0;

	top: 0;

	width: 110%;

	height: 100%;

	background: #fa9c28;

	z-index: 1;

	border-radius: 5px;

	transition: -webkit-transform 0.5s;

	transition: transform 0.5s;

	transition: transform 0.5s, -webkit-transform 0.5s;

	transition-timing-function: ease;

	-webkit-transform-origin: 0 0;

	        transform-origin: 0 0;

	transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);

	-webkit-transform: scaleX(0);

	        transform: scaleX(0);color:#ffffff;

}

.btn-yellow:hover::before {

	-webkit-transform: scaleX(1);

	        transform: scaleX(1);

	color: #fff !important;

	z-index: -1

}

.main-section{width:95%;margin:0 auto;}

header{position: fixed;

    z-index: 2;

    top: 0;

    left: 0;

    width: 100%;

    /* height: 80px; */

    background-color: transparent;

    box-shadow: 0 3px 6px 0 rgb(0 0 0 / 0%);

    z-index: 99;

    transition: all 1s}

.navbar-brand img{height:80px;}

.navber-padding{padding:0px 5px  !important;}

.btn-yellow{background-color:#FA8B01;padding: 23px 0px !important;min-width:226px;font-size:13px;box-shadow: 0 0 10px rgb(0 0 0 / 52%);;}



.heading-top{font-size: 29px;line-height:36px;margin-top: 0px;font-weight: 700;margin-bottom:12px;color: #ffffff;}

