						content: "";

						position: absolute;

						width: 100%;

						height: 1px;

						background: #cccccc ;

						top: 20px; /*half of height Parent (li) */

						left: -50%;

						z-index: -1;

					  }

					  

					  .progressbar li:first-child:after {

						content: none;

					  }

					  

					  .progressbar li.active:before {

						background: #58bb58;

						content: "";  

